import React, { Component } from 'react';

class Rating extends Component {
  render() {
    const final = [];
    for (var i=1; i<=5; i++) {
        if (this.props.rate >=i) {
            final.push(<div className="yellowStar col">&#9733;</div>);
        } else {
            final.push(<div className="yellowStar col">&#9734;</div>);
        }
    }
    
    return (
      <>
        <div className="row">
            <div className="col-12 col-md-3">
              <div className="ratingTitle">{this.props.text}</div>
              <div>{this.props.period}</div>
            </div>
            <div className="col-12 col-md-9 row">
                {final}                
            </div>
        </div>
      </>
    );
  }
}

export default Rating;
